import { html, PolymerElement } from '@polymer/polymer/polymer-element.js';
import { HAXWiring } from "@lrnwebcomponents/hax-body-behaviors/lib/HAXWiring.js"
/**
 * `haxschema-builder`
 * `dynamically build and visualize HAXschema`
 *
 * @microcopy - language worth noting:
 *  -
 *
 * @customElement
 * @polymer
 * @demo demo/index.html
 */
class HaxschemaBuilder extends PolymerElement {
  
  // render function
  static get template() {
    return html`
<style>:host {
  display: block;
}

:host([hidden]) {
  display: none;
}
</style>
<slot></slot>
<div>[[haxSchema]]</div>
<div>[[source]]</div>`;
  }

  // haxProperty definition
  static get haxProperties() {
    return {
  "canScale": true,
  "canPosition": true,
  "canEditSource": false,
  "gizmo": {
    "title": "Haxschema builder",
    "description": "dynamically build and visualize HAXschema",
    "icon": "icons:android",
    "color": "green",
    "groups": [
      "Builder"
    ],
    "handles": [
      {
        "type": "todo:read-the-docs-for-usage"
      }
    ],
    "meta": {
      "author": "btopro",
      "owner": "The Pennsylvania State University"
    }
  },
  "settings": {
    "quick": [
      {
        "property": "source",
        "description": "",
        "inputMethod": "textfield",
        "required": true,
        "icon": "icons:link",
        "validationType": "url"
      }
    ],
    "configure": [
      {
        "property": "haxSchema",
        "description": "",
        "inputMethod": "array",
        "required": false,
        "icon": "icons:android"
      },
      {
        "property": "source",
        "description": "",
        "inputMethod": "textfield",
        "required": true,
        "icon": "icons:link",
        "validationType": "url"
      }
    ],
    "advanced": []
  }
};
  }
  // properties available to the custom element for data binding
  static get properties() {
    return {
  "haxSchema": {
    "name": "haxSchema",
    "type": "Object",
    "value": "{}",
    "reflectToAttribute": false,
    "observer": false
  },
  "source": {
    "name": "source",
    "type": "String",
    "value": "",
    "reflectToAttribute": false,
    "observer": false
  }
};
  }

  /**
   * Store the tag name to make it easier to obtain directly.
   * @notice function name must be here for tooling to operate correctly
   */
  static get tag() {
    return "haxschema-builder";
  }
  /**
   * life cycle, element is afixed to the DOM
   */
  connectedCallback() {
    super.connectedCallback();
    this.HAXWiring = new HAXWiring();
    this.HAXWiring.setup(HaxschemaBuilder.haxProperties, HaxschemaBuilder.tag, this);
  }
  /**
   * life cycle, element is removed from the DOM
   */
  //disconnectedCallback() {}
  
}
window.customElements.define(HaxschemaBuilder.tag, HaxschemaBuilder);
export { HaxschemaBuilder };
